@import './src/style/scss/01_abstracts';

.courses-details-info {
  background-color: #f8f9f8;
  border-radius: 5px;
  padding: 12px;
  margin: 0;

  .image {
    text-align: center;
    position: relative;

    .link-btn {
      display: block;
      width: 100%;
      height: 100%;
      z-index: 3;
      position: absolute;
      left: 0;
      top: 0;
      cursor: pointer;
    }
    .content {
      position: absolute;
      left: 0;
      z-index: 2;
      right: 0;
      top: 50%;
      transform: translateY(-50%);

      i {
        display: block;
        width: 60px;
        height: 60px;
        background-color: $color-primary;
        color: $color-whitish;
        border-radius: 50%;
        line-height: 60px;
        font-size: 25px;
        margin: {
          left: auto;
          right: auto;
          bottom: 15px;
        }
      }
      span {
        display: inline-block;
        color: $color-whitish;
        border-radius: 5px;
        padding: 4px 20px;
        background-color: $color-blackish;
        font: {
          size: 17px;
          weight: 700;
        }
      }
    }
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      background-color: $color-blackish;
      opacity: 0.25;
    }
  }
  .info {
    margin-bottom: 0;
    list-style-type: none;
    padding: {
      left: 25px;
      right: 25px;
      top: 35px;
    }
    li {
      border-bottom: 1px solid #e5e5e7;
      color: $color-text-primary;
      font: {
        size: 17px;
        weight: 600;
      }
      padding: {
        top: 15px;
        bottom: 14px;
      }
      span {
        font-weight: 800;
        color: $color-blackish;
        position: relative;
        padding-left: 28px;

        i {
          color: $color-primary;
          position: absolute;
          left: 0;
          font-weight: normal;
          top: -2px;
          font-size: 20px;
        }
      }
      &.price {
        padding-bottom: 10px;
        color: $color-primary;
        font: {
          size: 28px;
          weight: 800;
        }
        span {
          color: $color-blackish;
          font: {
            size: 17px;
            weight: 800;
          }
        }
      }
      &:first-child {
        padding-top: 0;
      }
    }
  }
  .btn-box {
    margin-top: 35px;
    padding: {
      left: 25px;
      right: 25px;
    }
    .default-btn {
      display: block;
      width: 100%;
      padding: {
        right: 30px;
        left: 30px;
        top: 12px;
        bottom: 12px;
      }
      i {
        position: relative;
        left: 0;
        top: 2px;
        margin-right: 8px;
        line-height: 1;
        font: {
          weight: normal;
          size: 20px;
        }
      }
      &:not(:first-child) {
        margin-top: 15px;
        background-color: transparent;
        color: $color-blackish;
        border: 1px solid $color-blackish;

        &:hover {
          color: $color-whitish;
          border-color: $color-blackish;
        }
      }
    }
  }

  /* Max width 767px */
  @media screen and ($max-viewport-7) {
    .image {
      .content {
        i {
          width: 50px;
          height: 50px;
          line-height: 50px;
          font-size: 20px;
        }
        span {
          font-size: 14.5px;
        }
      }
    }
    .info {
      padding: {
        left: 10px;
        right: 10px;
        top: 30px;
      }
      li {
        font-size: 15px;
        padding: {
          top: 13px;
          bottom: 12px;
        }
        &.price {
          font-size: 20px;

          span {
            font-size: 15px;
          }
        }
      }
    }
    .btn-box {
      margin-top: 30px;
      padding: {
        left: 10px;
        right: 10px;
      }
    }
  }

  /* Min width 992px Max width 1199px */
  @media screen and ($viewport-9) and ($max-viewport-12) {
    .info {
      padding: {
        left: 15px;
        right: 15px;
      }
      li {
        font-size: 16px;

        &.price {
          font-size: 25px;
        }
      }
    }
  }
}

