@import './../01_abstracts';

html {
  font-size: pixelToEm($font-size-base-in-px, 16px); // 16px is font-size-browsers-default
}
body {
  font-family: $font-family-base;
  font-size: pixelToRem(14.5px);

  @include media-screen($viewport-7) {
    font-size: pixelToRem(15px);
  }

  @include media-screen($viewport-9) {
    font-size: pixelToRem(16px);
  }
}

p {
  line-height: 1.6;
}
